us: 100%;\n}\n.material-button, .material-button-fab\n{\n    background-color: var(--theme-main);\n    color: var(--theme-accent);\n}\n.material-button.light, .material-button-fab.light\n{\n    background-color: var(--theme-accent);\n    color: var(--theme-main);\n}\n*[class*=\"material-button\"], *[class*=\"material-button\"]::before\n{\n    text-decoration: none;\n    \n    box-sizing: border-box;\n    outline: none;\n\n    box-shadow: 0 0 5px rgba(0, 0, 0, 0.493);\n\n    transition: opacity 0.2s;\n}\n*[class*=\"material-button\"]::before\n{\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n\n    content: \"\";\n    background-color: var(--theme-accent);\n    opacity: 0;\n}\n*[class*=\"material-button\"].light::before\n{\n    background-color: var(--theme-main);\n}\n*[class*=\"material-button\"]:hover::before\n{\n    opacity: 0.04;\n}\n*[class*=\"material-button\"].light:hover::before\n{\n    opacity: 0.15;\n}\n/*Original code belongs to Google. Minor tweak by G. Leroy-Ferrec.*/\n@font-face {\n  font-family: 'Material Icons';\n  font-style: normal;\n  font-weight: 400;\n  src: url(https://fonts.gstatic.com/s/materialicons/v41/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');\n}\n.material-icons {\n  font-family: 'Material Icons';\n  font-weight: normal;\n  font-style: normal;\n  font-size: 24px;\n  line-height: 1;\n  letter-spacing: normal;\n  text-transform: none;\n  display: inline-table;\n  vertical-align: bottom; /* fix icon misaligning with text baseline in inline elements*/\n  white-space: nowrap;\n  word-wrap: normal;\n  direction: ltr;\n  -moz-font-feature-settings: 'liga';\n  -moz-osx-font-smoothing: grayscale;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */", '', '']]